/*公共样式*/
*{padding:0; margin:0;}
html,body{width:100%;height: 100%;}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden}
.clearfix{*zoom:1}
.cred{color:#f03e3e;}
.cgreen{color:#459d36;}
.hide{display:none;}
.fr{float:right;}
.fl{float: left;}
.rela{position: relative;}
.abs{position:absolute;}
h1{position: fixed; z-index:20; width: 100%; height:50px; line-height:50px; font-size:20px; left: 0; top: 0; background: #000000; color: #fff;}
.hello {
    height: 100%;
}
/*登录界面*/
.login-wrap{background:white;width:100%;height:100%; text-align:center;}
.login-con{padding-top: 50px;}
.login-con h3{margin-bottom: 20px;}
.login-con input{width:60%; display:block; margin:0 auto; height: 40px; line-height: 40px;}
.login-con button{width:60%;display:block; margin:0 auto; height: 40px; line-height:40px; border:none; background:#e32626ab; color:#fff; border-radius:5px;}
/*聊天界面*/
.chat-wrap{width: 100%; height: 100%;overflow-y:auto; background:white; text-align:center;}
.chat-con{padding: 5px 0 7px; background:white;}
.chat-con p{display:inline-block; padding:5px 10px; background:#999;border-radius:5px; color:#fff; }
.bottom{position:fixed;bottom:0; left: 0; width:100%; height: 50px; background: #fff;}
.bottom input{width: 78%; height: 30px; line-height: 50px; float:left;border:1px solid #eee;outline: none;}
.bottom button{width: 20%;height: 30px; float: right; border:none; background:#459d36;color: #fff;}
.chat-item{width:100%; margin-top:5px;}
.item-right .message{background: #62b900;position: relative;}
.item-left .message{background: #fff; margin-top:0px; position: relative;}
.item-left .img{margin-right:10px;}
.item-left .uname{font-size:12px; left:72px; top:11px;}
.chat-item .message{max-width:60%;display:block; padding:10px;border-radius:5px; margin-right:10px; text-align: justify;}
.chat-item .img{display:inline-block; width:40px; height:40px; background:url(/static/img/user.6dc856e.jpg) no-repeat; background-size:100% 100%;}
.item-right .message::after {
	content: "";
    position: absolute;
    width: 0px;
    height: 0px;
    right: -14px;
    top: 7px;
    border-width: 8px;
    border-style: solid;
    border-color: transparent transparent transparent rgb(98, 185, 0);
    -o-border-image: initial;
       border-image: initial;
}
.item-left .message::before {
	content: "";
    position: absolute;
    width: 0px;
    height: 0px;
    left: -13px;
    top: 6px;
    border-width: 8px;
    border-style: solid;
    border-color: transparent rgb(255, 255, 255) transparent transparent;
    -o-border-image: initial;
       border-image: initial;
}


